@font-face {
  font-family: 'Epilogue';
  src: url('./assets/fonts/Epilogue-ExtraBold.eot');
  src: url('./assets/fonts/Epilogue-ExtraBold.eot?#iefix') format('embedded-opentype'),
      url('./assets/fonts/Epilogue-ExtraBold.woff2') format('woff2'),
      url('./assets/fonts/Epilogue-ExtraBold.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

